import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/customer/mn-customer-home/mn-customer-home.vue'
import Shop from '../components/customer/mn-customer-shop/mn-customer-shop.vue'
import Product from '../components/customer/mn-customer-product/mn-customer-product.vue'
import Blog from '../components/customer/mn-customer-blog/mn-customer-blog.vue'
import Post from '../components/customer/mn-customer-post/mn-customer-post.vue'
import Cart from '../components/customer/mn-customer-cart/mn-customer-cart.vue'
import Layout from '../components/customer/mn-customer-layout/mn-customer-layout.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Layout,
    children:[
      {
        path:'/',
        component:Home,
        name:'Home'
      },
      {
        path:'/shop',
        component:Shop,
        name:'Shop'
      },
      {
        path:'/product',
        component:Product,
        name:'Product'
      },
      {
        path:'/blog',
        component:Blog,
        name:'Blog'
      },
      {
        path:'/post',
        component:Post,
        name:'Post'
      },
      {
        path:'/cart',
        component:Cart,
        name:'Cart'
      },
      {
        path: '*',
        name: 'catchAll',
        component: Home
      }
    ],
  },
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})
export default router


